/**
 * Dimensions
 */
export type TSuccessString = 'true' | 'false';
/**
 * Measures
 */
export type TSuccessNumber = 0 | 1;

export interface ISuccess<T extends TSuccessNumber | TSuccessString> {
    success?: T;
}
